<style>:root {
  --main-bg-color: brown;
}

:root {
  --color-background: #FFFFFF;
  --color-text: #000000;
  --color-primary-1: #D21034;
  --color-primary-2: #FFCE00;
  --color-primary-3: #006A4E;
}

:root {
  --main-bg-color: brown;
}

:root {
  --color-background: #FFFFFF;
  --color-text: #000000;
  --color-primary-1: #D21034;
  --color-primary-2: #FFCE00;
  --color-primary-3: #006A4E;
}

.controlRod {
  font-family: "xanh";
  background: rgba(0, 0, 0, 0.9);
  min-width: 30px;
  line-height: 30px;
  text-align: center;
  border: 1px solid white;
  margin: 4px 8px;
  color: white;
  position: relative;
  cursor: pointer;
  user-select: none;
  display: flex;
  flex-direction: row;
}

.active {
  border: 1px solid #BBB;
  color: #BBB;
}

.controlRod-display {
  display: flex;
  flex-direction: row;
  margin: 6px 0;
  writing-mode: vertical-lr;
  text-orientation: upright;
  text-transform: uppercase;
  letter-spacing: -3px;
}

.controlRod-icon {
  text-align: center;
  line-height: 30px;
  width: 30px !important;
  height: calc(100% - 30px);
  transition: transform 250ms ease-in-out;
  transform: translate(-4px, -15px);
  background: #006A4E;
  margin-right: 4px;
  /*filter: drop-shadow(0 0 10px $togoGreen);*/
}

.isAm .controlRod-icon {
  background: #10d9b3;
  /*filter: drop-shadow(0 0 10px $togoCyanDimmed);*/
}

.active .controlRod-icon {
  transform: translate(-4px, 25px);
  filter: none;
  content: "X";
  background: #ff628b;
  /*filter: drop-shadow(0 0 4px $togoPinkDimmed);*/
}

.active.isAm .controlRod-icon {
  background: #D21034;
  /*filter: drop-shadow(0 0 10px $togoRed);*/
}</style>

<script>
	import forEach from 'lodash/forEach';
	import map from 'lodash/map';
	import classNames from 'classnames';
	import { currentStore } from '../stores';

	export let active;
	export let displayName;
	export let onClick = () => undefined;
	let isAm;

	let className;

	$: {
		isAm = $currentStore.amDimension;
		className = classNames('controlRod', { active, isAm });
	}
</script>

<div class={className} on:click={onClick}>
	<div class="controlRod-icon">{' '}</div>
	<div class="controlRod-display">{displayName}</div>
</div>

