<style>:root {
  --main-bg-color: brown;
}

:root {
  --color-background: #FFFFFF;
  --color-text: #000000;
  --color-primary-1: #D21034;
  --color-primary-2: #FFCE00;
  --color-primary-3: #006A4E;
}

:root {
  --main-bg-color: brown;
}

:root {
  --color-background: #FFFFFF;
  --color-text: #000000;
  --color-primary-1: #D21034;
  --color-primary-2: #FFCE00;
  --color-primary-3: #006A4E;
}

.date-display {
  line-height: 20px;
  font-size: 20px;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.8);
  background: rgba(255, 255, 255, 0.7);
  padding: 8px 0 8px 4px;
  border: 1px solid white;
}

.date-year {
  display: inline-block;
}</style>

<script>
	import { gameStatus } from '../stores/matterStores';
	import getDateFromTicks from './logic/getDateFromTicks';
	import getYear from 'date-fns/getYear';
	import format from 'date-fns/format';

	let currentDate;

	$: {
		currentDate = getDateFromTicks($gameStatus.tickCount);
	}
</script>

<div class="date-display">
  <span class="panel-label">
  	Date:
  </span>
  <span class="date-year">
		{getYear(currentDate)}
	</span>
	<span class="date-month">
		{format(currentDate, 'MMM')}
	</span>
</div>
